<script setup lang="ts">
import PageSection from '@/components/PageSection.vue';
import { ProjectType } from '@/data/projects';
import { useText } from '@/data/strings';
import Interpolator from '@/utils/Interpolator';
import { onMounted, ref } from 'vue';

const hover = ref(0);
const selected = ref<number | null>(null);
const item = ref<number | null>(null);
const size = ref(20);
const text = useText();

const props = defineProps<{
    project: ProjectType,
}>();

onMounted(() => {
    size.value = Math.min(props.project.images.length, 20);
});

const handleMouseLeave = () => {
    hover.value = 0;
    selected.value = null;
}

const handleOver = (index: number) => {
    hover.value = 200;
    selected.value = index;
}

const getTransform = (index: number) => {
    const row = Math.floor((index - 1) % size.value);

    const x = hover.value ? 0 : 0;
    const y = selected.value == index ? 0 : (hover.value ? 50 : 100);
    const z = hover.value ? 400 : 600;

    return `translate3d(
                     ${Interpolator(row, [1, size.value], [x, 0])}px, 
                     ${Interpolator(row, [1, size.value], [y, 0])}px, 
                    -${Interpolator(row, [1, size.value], [z, 0])}px)`;

}

const handleClick = (index: number) => {
    item.value = item.value == null ? index : null;
}
</script>


<template>
    <PageSection class="mb-10">
        <div class="relative h-full w-full 4 flex flex-col md:flex-row">
            <div v-if="item" class="bg-surface-700/95 absolute inset-0 z-50" @click="item = null">
                <img :src="`${project.path}${project.images[item - 1]}`" class="flex object-contain w-full h-full" />
            </div>
            <div class="flex-1 md:flex-[2] overflow-auto z-30 pointer-events-none">
                <div class="flex flex-col gap-5 p-10">
                    <P class="font-extrabold text-primary/75 text-2xl md:text-4xl w-fit backdrop-blur-xl">
                        {{ text(project.name) }}
                    </P>
                    <p class="font-extrabold text-gray-500 text-sm md:text-xl backdrop-blur-xl">
                        {{ text(project.description) }}
                    </p>
                </div>
            </div>
            <div class="flex-1 md:flex-[3] h-full flex flex-wrap overflow-hidden" style="perspective: 800px; "
                @mouseout="handleMouseLeave">
                <div v-for="index in size" :key="index" :style="{
                    transition: 'all 500ms',
                    transform: getTransform(index),
                    filter: `brightness(${Interpolator((index - 1) % size, [1, size], [hover ? 0.7 : 0.5, 0.9])})`,
                    zIndex: index % size,
                }" class="bg-surface-400 w-[25%] h-[20%] md:w-[20%] md:h-[25%] flex justify-center items-center overflow-hidden shadow-2xl cursor-pointer border-white/75 border-[1px]"
                    @mouseover="handleOver(index)" @click="handleClick(index)">
                    <img width="150px" loading="eager" :src="`${project.path}s${project.images[index - 1]}`"
                        class="flex object-cover w-full h-full" />
                </div>
            </div>
        </div>
    </PageSection>
</template>