<script setup lang="ts">
import AppBackground from '@/components/AppBackground.vue';
import AppBar from '@/components/AppBar/AppBar.vue';
import text from '@/data/strings';
import { onMounted, onUnmounted, provide, ref } from 'vue';

const theme = ref("dark");
const lang = ref<"en" | "ar">("en");

const scroll = ref(0);

const handleThemeToggle = () => {
    theme.value = theme.value == "dark" ? "light" : "dark";
}

onMounted(() => {
    lang.value = (window.localStorage.getItem("lang") as "en" | "ar" | null) ?? "en";

    const handleScroll = () => {
        scroll.value = window.scrollY;
    }

    window.addEventListener("scroll", handleScroll);

    onUnmounted(() => {
        window.removeEventListener("scroll", handleScroll);
    });
});

const handleLangToggle = () => {
    lang.value = lang.value == "ar" ? "en" : "ar";
    window.localStorage.setItem("lang", lang.value);
}

provide("text", (str: string | { ar: string, en: string }, params: { [kye: string]: string }) => text(str, lang.value, params));
provide("lang", lang.value);
provide("scroll", scroll);
</script>


<template>
    <div :class="`${theme} text-primary`" :dir="lang == 'ar' ? 'rtl' : 'ltr'">
        <AppBackground class="-z-10" />
        <AppBar @themeChange="handleThemeToggle" :theme="theme" @langChange="handleLangToggle" :lang="lang"
            class="app-bar" />
        <div class="w-full flex flex-col items-center">
            <router-view />
        </div>
    </div>
</template>

<style scoped>
.app-bar {
    transform: translateY(-300%);
    animation: appBar 2s forwards;
}

@keyframes appBar {
    0% {
        transform: translateY(-300%);
    }

    100% {
        transform: translateY(0%);
    }
}
</style>