<script setup lang="ts">
import { onMounted, onUnmounted, ref } from 'vue';
import interpolate from "@/utils/Interpolator";
import AppBarItem from './AppBarItem.vue';
import DarkIcon from '@/icons/DarkIcon.vue';
import LightIcon from '@/icons/LightIcon.vue';
import { useText } from '@/data/strings';
import HomeIcon from '@/icons/HomeIcon.vue';
import UserIcon from '@/icons/UserIcon.vue';
import ProjectsIcon from '@/icons/ProjectsIcon.vue';

const scroll = ref(0);
const text = useText();

defineProps<{
    theme: string,
    lang: "en" | "ar",
}>();

defineEmits<{
    themeChange: [],
    langChange: [],
}>();

onMounted(() => {
    const handleScroll = () => {
        scroll.value = window.scrollY;
    }

    window.addEventListener("scroll", handleScroll);

    onUnmounted(() => {
        window.removeEventListener("scroll", handleScroll);
    });
});

</script>

<template>
    <nav class="w-full sticky top-0 z-20" dir="ltr" :style="{
        padding: `${interpolate(scroll, [0, 200], [30, 0])}px`,
    }">
        <div class="w-full rounded-xl border-black h-14 bg-surface-200 border-divider border-[1px] flex justify-center items-center"
            :style="{
                borderRadius: `${interpolate(scroll, [0, 200], [0.75, 0])}rem`,
                borderWidth: `${interpolate(scroll, [50, 200], [.5, 0])}px`,
                transform: `translateY(${interpolate(scroll, [330, 550], [0, -100])}%)`
            }">

            <RouterLink to="/" class="h-full">
                <AppBarItem :icon="HomeIcon" :title="text('home')" />
            </RouterLink>
            <RouterLink to="/projects" class="h-full">
                <AppBarItem :icon="ProjectsIcon" :title="text('projects')" />
            </RouterLink>
            <RouterLink to="/about" class="h-full">
                <AppBarItem :icon="UserIcon" :title="text('about')" />
            </RouterLink>

            <div class="h-full border-divider/20 border-[1px] "></div>

            <AppBarItem @click="$emit('langChange')" :title="text(lang == 'ar' ? 'en' : 'ar')" />

            <AppBarItem @click="$emit('themeChange')" :icon="theme == 'light' ? DarkIcon : LightIcon" />
        </div>
    </nav>
</template>


<style scoped>
.root {
    transform: translateY(300%);
    animation: appBar 2s forwards;
    animation-delay: 5s;
}

@keyframes appBar {
    0% {
        transform: translateY(300%);
    }

    100% {
        transform: translateY(0%);
    }
}


@keyframes opacity {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }

}
</style>