<script setup lang="ts">
import PageHeader from '@/components/PageHeader.vue';
import PageSection from '@/components/PageSection.vue';
import projects from '@/data/projects';
import { useText } from '@/data/strings';
import { user } from '@/data/user';
import Interpolator from '@/utils/Interpolator';
import { computed, ref } from 'vue';
import ViewBase from './ViewBase.vue';
import Button from '@/components/Button.vue';
import RightArrowIcon from '@/icons/RightArrowIcon.vue';

const text = useText();
const hover = ref(0);

const images = computed(() => {
  const ps = Object.values(projects).map(v => v.images.map(i => v.path + "s" + i));
  const res = [];

  for (var i = 0; i < 25; ++i) {
    res.push(ps[Math.floor(Math.random() * 10) % ps.length][Math.floor(Math.random() * 10) % ps.length]);
  }
  return res;
});


</script>

<template>
  <ViewBase>
    <PageHeader :title="text('my name', { name: text(user.name) })">
      <p class="font-extrabold text-gray-500 text-sm md:text-xl">
        {{ text('welcome_message') }}
      </p>
    </PageHeader>

    <PageSection class="mb-10">
      <div class="relative h-full w-full 4 flex flex-col md:flex-row">
        <div class="flex-1 z-50 pointer-events-auto">
          <div class="absolute inset-0 max-w-2xl flex flex-col p-10">
            <div class="flex-1 flex flex-col gap-5">
              <P class="font-extrabold text-gray-200 text-2xl md:text-4xl backdrop-blur-2xl w-fit">
                {{ text('For my information and CV') }}
              </P>
              <RouterLink to="/about">
                <Button :icon="RightArrowIcon" title="TO MY INFORMATION"></Button>
              </RouterLink>
            </div>
            <div class="flex-1 flex  flex-col gap-5">
              <h2 class="font-extrabold text-gray-200 text-2xl md:text-4xl backdrop-blur-2xl w-fit">
                {{ text('Or take a look at my projects') }}
              </h2>
              <RouterLink to="/projects">
                <Button :icon="RightArrowIcon" title="MY PROJECT"></Button>
              </RouterLink>
            </div>
          </div>
        </div>
        <div class="flex-[2] h-full flex flex-wrap overflow-hidden" style="perspective: 1000px; "
          @mouseover="hover = 200" @mouseout="hover = 0">
          <div v-for="index in 25" :key="index" :style="{
            transition: 'all 2s',
            transform: `translateZ(-${Interpolator(index % 6, [1, 6], [800 - hover, 10])}px)`,
            filter: `brightness(${Interpolator(index % 6, [1, 6], [0.6 + (hover / 5000), 0.9])})`,
            zIndex: index % 6,
          }" class="w-[20%] h-[20%] flex justify-center items-center overflow-hidden shadow-2xl">
            <img :src="images[index - 1]" class="flex w-full h-full object-cover transition-all" />
          </div>
        </div>
      </div>
    </PageSection>

  </ViewBase>
</template>