export default function text(str: string | { ar: string, en: string }, lang: "ar" | "en", params: { [kye: string]: string }): string {
    try {

        if (typeof str == "object") {
            return str[lang];
        }

        let s = (strings[str][lang] ?? str);

        if (params) {
            Object.keys(params)?.forEach(key => {
                s = s.replace(`:name`, params[key]);
            });
        }


        return s;
    } catch {
        return typeof str == "string" ? str : str['en'];
    }
}

export const useText = () => inject<(s: string | { ar: string, en: string }, params?: { [kye: string]: string }) => string>('text', (str) => typeof str == "string" ? str : str['en']);