<script setup lang="ts">
import { onMounted, onUnmounted, ref } from 'vue';

const pos = ref<{ x: number, y: number }>({ x: 0, y: 0 });



onMounted(() => {
    const handleMove = (e: MouseEvent) => {
        pos.value = {
            x: e.clientX,
            y: e.clientY,
        };
    }

    window.addEventListener("mousemove", handleMove);


    onUnmounted(() => {
        window.removeEventListener("mousemove", handleMove);
    });
});

</script>


<template>
    <div class="fixed inset-0 bg-surface-0">
        <div class="background mask w-[600px] h-[600px] absolute opacity-10"
            :style="{ top: `${pos.y - 300}px`, left: `${pos.x - 300}px`, backgroundPosition: `${-pos.x}px ${-pos.y}px` }">

        </div>
        <div class="background w-full h-full absolute opacity-[0.03]">

        </div>
    </div>
</template>


<style scoped>
.background {
    background: url("/src/assets/back.svg");
    background-repeat: repeat;
    background-size: 200px;
    background-position: -300px -300px;
}

.mask {
    mask-image: radial-gradient(#000, transparent 80%);
}
</style>